/* The switch - the box around the slider */
.switch {
	width: 64px;
	height: 34px;
	position: relative;
	display: inline-block;
}
/* Hide default HTML checkbox */
.switch input {
	width: 0;
	height: 0;
	opacity: 0;
}
/* The slider */
.slider {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #ccc;
	transition: .4s;
	cursor: pointer;
	-webkit-transition: .4s;
}
.slider:before {
	width: 26px;
	height: 26px;
	position: absolute;
	bottom: 4px;
	left: 4px;
	background-color: white;
	transition: .4s;
	content: '';
	-webkit-transition: .4s;
}
input:checked + .slider {
	background-color: rgb(102, 255, 255, .95);
}
input:focus + .slider {
	box-shadow: 0 0 1px #2196f3;
}
input:checked + .slider:before {
	transform: translateX(26px);
	-webkit-transform: translateX(26px);
	-ms-transform: translateX(26px);
}
/* Rounded sliders */
.slider.round {
	border-radius: 34px;
}
.slider.round:before {
	border-radius: 50%;
}
